.App {
  text-align: center;
  border: 1px solid red;
  max-width: 1500px;
  height: 100vh;
  margin: 0 auto;
}

.circle {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(0, -50%);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: red;
  margin: 0 auto;
  transition: .5s;
}





